<template>
	<view class="container">
		<!-- <view class="intro">本项目已包含uni ui组件，无需import和注册，可直接使用。在代码区键入字母u，即可通过代码助手列出所有可用组件。光标置于组件名称处按F1，即可查看组件文档。</view>
		<text class="intro">详见：</text>
		<uni-link :href="href" :text="href"></uni-link> -->
		<view class="mymap" id="mymap">			
		</view>
		<!-- 顶部导航 -->
		<view class="navBar">
			<view class="navtop">变化发现</view>
			<view class="navtop">领取任务</view>	
		</view>
		<view class="rightToll">
			<view class="tollone">
				<view class="uni-list" v-show="!layersFrame">
					<radio-group @change="radioChange">
						<label class="uni-list-cell" v-for="(item, index) in radioitems" :key="item.value">
							<view>
								<radio :value="item.value" :checked="index === current" />
							</view>
							<view>{{item.name}}</view>
						</label>
					</radio-group>
				</view>
				<view class="layersControl tollIcon" @click="layerframeControl"></view>
			</view>
			<view class="tolltwo">
				<view class="uni-list" v-show="!datasFrame">
					<checkbox-group @change="checkboxChange">
						<label class="uni-list-cell" v-for="item in checkboxitems" :key="item.value">
							<view>
								<checkbox :value="item.value" :checked="item.checked" />
							</view>
							<view>{{item.name}}</view>
						</label>
					</checkbox-group>
				</view>
				<view class="datassControl tollIcon" @click="datasframeControl"></view>
			</view>
			<view class="tollthree">
				<view class="localControl tollIcon" @click="localtionControl"></view>
			</view>
		</view>
		<view class="bottomBar">
			<span>消息</span><span>我的</span>
		</view>
	</view>
</template>

<script>
	import esriLoader from 'esri-loader';
		export default {
			data() {
				return {
					map:null,
					showLayer:true,
					layersFrame:true,
					datasFrame:true,
					href: 'https://uniapp.dcloud.io/component/README?id=uniui',
					radioitems: [
						{
							value: 'imgMap',
							name: '卫星地图',
							checked: 'true'
						},
						{
							value: 'skyMap',
							name: '天地图'							
						},
					],
					checkboxitems: [
						{
							value: 'poi',
							name: 'POI'
						},
						{
							value: 'house',
							name: '房屋'							
						},
						{
							value: 'road',
							name: '道路'							
						},
					],
					current: 0
				}
			},
			mounted() {
				this.creatMapview();//创建地图
			},
			beforeDestroy(){
				if(this.view){
					this.view.container = null;
				}
			},
			methods: {
				creatMapview(){
					esriLoader.loadModules(['esri/Map','esri/views/MapView','esri/layers/FeatureLayer',"esri/layers/TileLayer","esri/layers/WebTileLayer"],{
						css:true
					})
					.then(([Map,MapView,FeatureLayer,TileLayer,WebTileLayer])=>{
						let map = new Map({
							// basemap:'streets',
						});
						this.map=map;
						let view = new MapView({
							container:'mymap',
							map:map,
							center:[116.29845,39.95933],
							zoom:12,
							// zoom:5,
						});
						view.ui._removeComponents(['zoom']);//去掉缩放图标
						view.ui._removeComponents(["attribution"]);//去掉ersilogo
						let imageLayer=new TileLayer({
							//影像服务"esri/layers/ImageryLayer"
							// url: "https://landsat2.arcgis.com/arcgis/rest/services/Landsat8_Views/ImageServer"
							
							//MapServer"结尾；"esri/layers/MapImageLayer"动态服务，无切片信息；esri/layers/TileLayer"或者"esri/layers/WebTileLayer"切片服务有切片信息 	
							url:"https://elevation3d.arcgis.com/arcgis/rest/services/NatGeo_World_Map/MapServer"
							// url: "http://192.168.1.163:6080/arcgis/rest/services/CYImage/MapServer"
						  });
						// let trailheadsLayer=new FeatureLayer({
						// 	url:'https://services3.arcgis.com/GVgbJbqm8hXASVYi/arcgis/rest/services/Trailheads/FeatureServer/0'//要素服务
						// });
						// view.map.add(trailheadsLayer);
						 map.add(imageLayer);//影像切片图
						 let webTile=new WebTileLayer({
						   urlTemplate: 'https://stamen-tiles-{subDomain}.a.ssl.fastly.net/terrain/{level}/{col}/{row}.png',
						   subDomains: ['a', 'b', 'c', 'd'],
						   visible:false
						 });
						  map.add(webTile);//天地图
						  const layerfeature = new FeatureLayer({
						    // URL to the service
						    url: 'https://services3.arcgis.com/GVgbJbqm8hXASVYi/arcgis/rest/services/Trailheads/FeatureServer/0'
						  });
						  map.add(layerfeature);//天地图
						 const symbol = {
						   type: "picture-marker",  // autocasts as new PictureMarkerSymbol()
						   url: "data:image/svg+xml;base64,",
						   width: "20px",
						   height: "20pxpx"
						 };
						 const graphics = [{
							 geometry: {
								 type: 'point',
								 longitude: 116.29845, // 经度116.29845,39.95933
								 latitude: 39.95933, // 纬度
							 }
						 }];
						  // 设置渲染
						  const renderer = {
							  type: 'simple',
							  symbol,
						  };
						  // 创建要素图层
						  const layer = new FeatureLayer({
							  source: graphics,
							  objectIdField: 'ObjectID',
							  renderer,
						  });
						  map.add(layer);
						 // if (navigator.geolocation) {
							//  navigator.geolocation.getCurrentPosition(this.geoSuccess, this.geoError);
							//  // 支持
							//  console.log("支持地理位置接口");
						 // } else {
							//  // 不支持
							//  console.log("不支持地理位置接口");
						 // }
		
						 // view.map.addMany([imageLayer,trailheadsLayer])
					})
				},
				radioChange(evt) {
					if(evt.target.value=="imgMap"){
						this.map.layers.items[0].visible=this.showLayer
						this.map.layers.items[1].visible=!this.showLayer
					}else{
						this.map.layers.items[0].visible=!this.showLayer
						this.map.layers.items[1].visible=this.showLayer
					}
				},
				layerframeControl(){
					this.layersFrame=!this.layersFrame;
				},
				checkboxChange(e) {
					var items = this.items,
						values = e.detail.value;
					for (var i = 0, lenI = items.length; i < lenI; ++i) {
						const item = items[i]
						if(values.includes(item.value)){
							this.$set(item,'checked',true)
						}else{
							this.$set(item,'checked',false)
						}
					}
				},
				datasframeControl(){
					this.datasFrame=!this.datasFrame
				},
				localtionControl(){
					// uni.getLocation({
					//     type: 'wgs84',
					//     success: function (res) {
					//         console.log('当前位置的经度web：' + res.longitude);
					//         console.log('当前位置的纬度web：' + res.latitude);
					//     },
					// 	fail:function(err){
					// 		debugger
					// 	}
					// });
					// 通过手机获取位置，传递回来，更新坐标图标
					let jumpUrl = window.location;
					 //传递多个参数时用&连接
					let path = `/pages/index?jumpUrl=${jumpUrl}`;debugger
					uni.navigateTo({
						 url: path
					})
				},
				// //同意授权				 
				// geoSuccess(event) {debugger
				//    console.log(`纬度:`+event.coords.latitude + ', ' +`经度:`+ event.coords.longitude);
				//   console.log('chenggong')
				// },
				// //拒绝授权	 
				// geoError(error) {
				// 	console.log(error)
				// 	console.log("Error code " + error.code + ". " + error.message);
				// 	switch (error.code) {
				// 		case error.PERMISSION_DENIED:
				// 			alert("定位失败,用户拒绝请求地理定位");
				// 			break;
				// 		case error.POSITION_UNAVAILABLE:
				// 			alert("定位失败,位置信息是不可用");
				// 			break;
				// 		case error.TIMEOUT:
				// 			alert("定位失败,请求获取用户位置超时");
				// 			break;
				// 		case error.UNKNOWN_ERROR:
				// 			alert("定位失败,定位系统失效");
				// 			break;
				// 	}
				// }
			}
		}
</script>

<style>
	uni-page-head{display: none!important;}
	uni-page-body{    
		/* height: calc(100% - 44px);
		height: calc(100% - 44px - constant(safe-area-inset-top));
		height: calc(100% - 44px - env(safe-area-inset-top)); */
		height: 100%;
	}
	.container {
		/* padding: 20px; */
		font-size: 14px;
		line-height: 24px;
		width: 100%;
		height: 100%;
	}
	.mymap{
		height: calc(100% - 44px);
		height: calc(100% - 44px - constant(safe-area-inset-top));
		height: calc(100% - 44px - env(safe-area-inset-top));		
		}
	.bottomBar {
		position: fixed;
		display: flex;
		justify-content: space-around;
		left: 0;
		right: 0;
		bottom: 0;
		height: 44px;
		line-height: 44px;
	}
	.navBar{position: absolute;left: 0;top: 20px;color: #ffffff;opacity: 0.7;}
	.navtop{padding: 5px 10px;background-color:#40bbf3;margin-bottom: 10px;border-radius: 2px;}
	/* 右侧工具 */
	.rightToll{position: absolute;right: 0;top: 20px;}
	.tollone{height: 72px;position: relative;}
	.tolltwo{height: 100px;position: relative;margin: 5px 0;}
	.tollthree{height: 72px;position: relative;}
	.uni-list{width: 100px;border: solid 1rpx #40BBF3;border-radius: 5px;}
	.uni-list-cell {display: flex;justify-content: flex-start;padding: 5px 0;}
	.uni-list radio,.uni-list checkbox{transform:scale(0.8)}
	.tollIcon{width: 18px;height: 18px;position: absolute;right: 5px;top: 50%;margin-top: -9px;}
	.layersControl{background-image: url(../../static/layers.svg);}
	.datassControl{background-image: url(../../static/datas.svg);}
	.localControl{background-image: url(../../static/closer.svg)};
</style>
